import { Container, ContentContainer } from "./Home.styles";
import { Form, Header, List } from "../components";
import { useEffect, useState } from "react";
import axios from "axios";
import { Car } from "../types";
export const BASE_URL = "localhost:8080";
const Home = () => {
  const aaa: Car = {
    brand: "string",
    plate: "string",
    weight: 12,
    carId: { id: "CarId" },
    entryTime: "string",
  };
  const [carList, setCarList] = useState<Car[] | null>([aaa]);

  useEffect(() => {
    axios.get(`${BASE_URL}/parking`).then((result) => setCarList(result.data));
  }, []);

  return (
    <Container>
      <Header />
      <ContentContainer>
        <Form />
      </ContentContainer>
      <List carList={carList} />
    </Container>
  );
};

export default Home;
