import { Typography } from "@mui/material";
import { ContentContainer } from "../../view/Home.styles";

const HEADER_TEXT =
  "Witaj nasz szanowny pracowniku! Przygotuj si do swojej zmiany chcemy aby czu si dobrze w swoim zawodzie <3 Przypominamy by sprawdza czy samochody s stabilne na platformie.";

const Header = () => {
  return (
    <ContentContainer>
      <Typography variant="h5">{HEADER_TEXT}</Typography>
    </ContentContainer>
  );
};

export default Header;
