import { useState } from "react";
import { StyledTextField, Container } from "./Form.styles";
import { Button } from "@mui/material";
import axios from "axios";
import { BASE_URL } from "../../view/Home";

const Form = () => {
  const [brand, setBrand] = useState<string>("");
  const [plate, setPlate] = useState<string>("");
  const [weight, setWeight] = useState<number>(0);

  const Submit = () => {
    axios
      .post(`${BASE_URL}/parking`, {
        brand: brand,
        plate: plate,
        weight: weight,
      })
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  return (
    <Container>
      <StyledTextField
        variant="outlined"
        value={plate}
        placeholder="Rejestracja"
        label="Rejestracja"
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          setPlate(event.target.value);
        }}
      ></StyledTextField>
      <StyledTextField
        variant="outlined"
        value={brand}
        placeholder="Marka"
        label="Marka"
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          setBrand(event.target.value);
        }}
      ></StyledTextField>
      <StyledTextField
        variant="outlined"
        value={weight}
        label="Waga"
        type="number"
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
          setWeight(parseInt(event.target.value));
        }}
      ></StyledTextField>
      <Button variant="contained" onClick={Submit}>
        Dodaj
      </Button>
    </Container>
  );
};

export default Form;
