import { Typography } from "@mui/material";
import { ListContainer, ListElement } from "./List.styles";
import axios from "axios";
import { BASE_URL } from "../../view/Home";
import { Car } from "../../types";

export interface ListProps {
  carList: Car[] | null;
}

const List = ({ carList }: ListProps) => {
  const deleteCar = (id: string) => {
    console.log();
    axios
      .delete(`${BASE_URL}/parking/${id}`)
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const listItems =
    !!carList && !!carList.length ? (
      carList.map((car) => (
        <ListElement key={car.carId.id} onClick={() => deleteCar(car.carId.id)}>
          <Typography variant="body1">Marka: {car.brand}</Typography>
          <Typography variant="body1">Rejestracja:{car.plate}</Typography>
          <Typography variant="body1">Czas wjazdu: {car.entryTime}</Typography>
          <Typography variant="body1">Waga: {car.weight}</Typography>
        </ListElement>
      ))
    ) : (
      <Typography variant="h5">Brak aut na parkingu</Typography>
    );
  return <ListContainer>{listItems}</ListContainer>;
};

export default List;
